h1 {
    display: none;;

}

header {
    background-color: #1c70cf;
    padding: 20px 0 20px 0;
    display: flex;
    justify-content: center;
   box-shadow:0px 2px 2px #06376f;
}
.search-form {
    display: flex;
    
}

.search-form input{
    width: 300px;
    height: 40px;
    border-radius: 15px 0 0 15px;
    padding-right: 5px;
    padding-left: 15px;
    border-color: rgb(0, 255, 255);
      border: -20px;
      background-color: rgb(205, 234, 243);
      &:hover{
        border-color:rgb(0, 255, 255); ;
      }
}
.search-form button{
    width: 50px;
    height: 40px;
    border-radius: 0 15px 15px 0;
    padding: 0;
    margin: 0;
    padding-left: 0;
    border: 1px;
    text-align: center;
    background-color: #fcf93e;
    border-color: rgb(0, 162, 255);
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.gallery-item>img {
    height: 200px;
}
.photo-card {
    margin: 15px;
    border: 1px solid #eeeeee;
    box-shadow: 0px 1px 1px #434547;
}
.info {
    display: flex;
    justify-content: space-around;
}
.info-item {
    text-align: center;
    line-height: 1.7;
    
    }
.load {
    display: flex;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: 25px;
}
.load .load-more{
    color: #eeeeee;
    background-color: #065ba0;
     border-radius: 3px;
     width: 120px;
     height: 30px;
      border: 1px;
}
.is-hidden {
  display: none;
}

.uni{
  display: flex;
  justify-content: center;
  font-size: 50px;
  margin-bottom: 15px;
  padding-bottom: 20px;
  transform: rotate(280deg) translateY(5px) translateX(18px);
}

